@import "./variables";

body {
    margin: 0;
    padding: 0;
}

a {
    color: currentColor;
    text-decoration: none;
}

h1, 
h2, 
h3, 
h4 {
    margin: 0;
    padding: 0;
    font-weight: 700;
}

ul {
    @include reset-list();
}

img {
    max-width: 100%;
}

p {
    margin: 0;
}

button {
    font-size: 15px;
}