@import "./variables";

.container {
  --padding-left: 20px;
  --padding-right: 20px;

  padding-left: var(--padding-left);
  padding-right: var(--padding-right);

  @media (min-width: $breakpoint-desktop) {
    max-width: $breakpoint-desktop;
    margin-left: auto;
    margin-right: auto;
  }
}